<template>
    <b-form-select
            :value="valueSelected"
            :options="options"
            class="mb-3"
            :disabled="disabled"
            style="font-size: 0.75rem; border-radius: 0;"
            @input="fireParentEvent"
    ></b-form-select>
</template>

<script>
    export default {
        name: "DropboxFilter",
        props:{
            valueSelected: {
                required: true,
            },
            options: {
                required: true,
            },
            disabled:{
                required:true,
            }
        },
        methods:{
            fireParentEvent : function(val){
                this.$emit('input', val);
            }
        }
    }
</script>

